function mergeArray(...arrays) {
  return [].concat(...arrays);
}
const arr1 = [1, 2, 3];
const arr2 = [4, 5, 8];
const arr3 = [7, 6, 9];
const arr4 = [7, 8, 9];
const mergedArray = mergeArray(arr1, arr2, arr3, arr4);
console.log(mergedArray);

function sortByLength(arr) {
  return arr.sort((a, b) => a.length - b.length);
}

const arr = ["apple", "banana", "kiwi", "orange", "grape"];
const sortedArr = sortByLength(arr);
console.log(sortedArr);
